.wrapper {

  height: 100vh;

  width: 100%;

  display: flex;

  flex-direction: column;

  justify-content: center;

  align-items: center;

  background-color: #32CD32;

}
.mainapp {

overflow: hidden;
  background-image: url("./images/background.jpg");
  height: 100vh;
  align-items: center;
background-position:50% 30%;
  background-repeat: no-repeat;
  background-size: cover;
}
.loginPage {



  display: flex;
  
  background-repeat: no-repeat;
  background-size: cover;

}
.login-wrapper {
  height:40%;
  width: 25%;
  display: flex;
  margin-left: 70%;
  margin-top: 10%;
  flex-direction: column;

  padding: 8px 20px;

  border-radius: 10px;

  box-shadow: 0px 10px 50px #555;

  background-color: #ffffff;

}
.loading {

  height: 100vh;

  width: 100%;

  display: flex;

  flex-direction: column;


  align-items: center;

  background-color: white;

}

.link{
  color: blue;
}
.form-wrapper {

  width: 40%;
  display: flex;

  flex-direction: column;

  padding: 20px 40px;

  border-radius: 10px;

  box-shadow: 0px 10px 50px #555;

  background-color: #ffffff;

}
.buttonbox {

  width: 46%;
  height: 6%;
  display: flex;
  margin-bottom: 15px;
  background-color: #519e8a;
  box-shadow: 0px 10px 50px #555;
  border-radius: 10px;



}
.buttonbox button{
    width: 50%;
      margin-bottom: 0.25em;
    border-radius: 1px;

    height: 100%;
  background-color: #32CD32;


}

.buttonbox button:hover {

  color: #519e8a;

  background-color: #fff;

  border: 2px solid #519e8a;

}
.dropdown {

  width: 20%;

  display: flex;

  flex-direction: column;

  padding: 20px 40px;

  border-radius: 10px;

  box-shadow: 0px 10px 50px #555;

  background-color: #ffffff;

}

form {

  width: 100%;

  display: flex;

  flex-wrap: wrap;

}



h1 {

  text-align: center;

  width: 100%;

  color: #111;

  font-weight: lighter;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,

    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

}



label {

  font-size: 0.8em;

  margin-bottom: 0.25em;

  color: #222;

  font-weight: lighter;

}



input {

  padding: 10px 10px;

  border-radius: 5px;

  outline: none;

  border: 1px solid #cfcfcf;

}



input::placeholder {

  font-size: 1.2em;

  font-weight: lighter;

  color: #999;

}



input.error {

  border: 1px solid red;

}



.errorMessage {

  color: red;

  font-size: 0.75em;

  display: relative;

}

.dropdown{
  flex-direction: column;
  margin-bottom: 15px;
  width: 100%;
}

.firstName {

  margin-right: 1%;

}



.lastName {

  margin-left: 1%;

}



.firstName,

.lastName,

.email,

.password {

  display: flex;

  flex-direction: column;

  margin-bottom: 15px;

}

.lemail,

.lpassword {

  display: flex;

  flex-direction: column;

  margin-bottom: 15px;
  width: 95%;

}




.email,

.password {

  width: 100%;

}



.login {

  width: 95%;


  flex-direction: column;


}



.login button {

  background-color: #519e8a;
  color: #fff;
  border-radius: 5px;

  border: 2px solid #fff;
  float: right;
  width: 30%;
  padding-left: 10%;
  margin-top: 0.25em;

  padding: 8px 3px;

  font-size: 1em;

  font-weight: lighter;

  letter-spacing: 1px;

  margin-bottom: 1em;

}



.login button:hover {

  color: #519e8a;

  background-color: #fff;

  border: 2px solid #519e8a;

}

.forgot{
  width: 100%;
  align-items: center;
  padding-left: 30%;
}
.login small {

  color: #999;

  font-weight: lighter;

}
.createAccount {

  width: 100%;

  display: flex;

  flex-direction: column;

  align-items: center;

}



.createAccount button {

  background-color: #519e8a;

  color: #fff;

  border: 2px solid #fff;

  width: 100%;

  margin-top: 1em;

  padding: 8px 0px;

  font-size: 1em;

  font-weight: lighter;

  letter-spacing: 1px;

  margin-bottom: 0.25em;

}



.createAccount button:hover {

  color: #519e8a;

  background-color: #fff;

  border: 2px solid #519e8a;

}



.createAccount small {

  color: #999;

  font-weight: lighter;

}