.notificationcontainer {
  display: grid;
  grid-auto-flow: row;
  max-width: 93.7%;
  min-height: 40%;
  border-radius: 6px;
  margin-top: 10px;
  background: #FFF;
}
.Deal {
  display: flex;
  flex-direction: column;
  max-width: 80%;
  min-height: 40%;
  margin-left: 4vw;
  border-radius: 6px;
  margin-top: 10px;
  background: #FFF;
}
.notificationlist {
    display: flex;
    flex-direction: column;
    padding: 10 10px;
    overflow-y: scroll;

  }
.reverse {
  display: flex;
  right: 120px;
  overflow: hidden;
  
}
.badge{
  display: flex;
  overflow:hidden;
  flex-direction: row;
   justify-content: flex-end;
   margin-right: 12px;

}
.search {
  width: 37vw;
  height:2vh;
  margin-top: 55px;
  border: 2px solid #fff;
  border-radius: 25px;
  right: 70px;
}
.notifheader {
    display: flex;
    flex-direction: row;
    height: auto;
