  margin-top: 6px;
  margin-left: 2vw;

}
.filtercard{
    flex-direction:column;
    min-width: 19%;
    max-width: 19%;
    height:auto;
    position: fixed;
    overflow: hidden;
    border-radius: 6px;
    margin-left: 3.2vw;
    background: #FFF;
}
.aboutheader { grid-area: header; }
.button { grid-area: button; }
.body { grid-area: main; }
.item4 { grid-area: righte; }
.item5 { grid-area: footer; }
.aboutcard{
  margin-left: 4vw;
    min-width:55vw;
    max-width:55vw;
    display: grid;
   grid-template-areas:
    'header header header header header photo'
    'main main main main main button'
    'footer footer footer footer footer button';
    border-radius: 6px;
    margin-bottom:40;
    overflow: hidden;
    height:73vh;
    background: #FFF;

}
.overallcard{
    margin-left: 3vh;
    min-width:55vw;
    max-width:55vw;
    display: grid;
   grid-template-areas:
    'header header header header header photo'
    'main main main main main button'
    'footer footer footer footer footer button';
    border-radius: 6px;
    margin-bottom:40;
    overflow: hidden;
    min-height:20vh;
    max-height:20vh;
    background: #FFF;

}
.button{
  display: flex;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  padding-right: 9px;
  padding-bottom: 7px; 

}
.submit{
  display: flex;
  overflow: hidden;
  flex-direction: row-reverse;
  align-items: flex-end;
   padding-right: 13px;


}
.body{
   overflow-y: auto;
   display: flex;
   margin-top: 20px;
   
   overflow-x: hidden;
   flex-direction: column;
}
.photo{ 
  grid-area: photo;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  padding-right: 27px;
}
.aboutheader {
    display: flex;
    grid-area: header;
    padding-top: 0px;
    min-height: 130px;
    margin-left: 13px;
    max-height: 130px;
    flex-direction: column;
   
  }
  .head {
    display: flex;
    grid-area: head;
    padding-top: 0px;
    min-height: 130px;
    margin-left: 13px;
    max-height: 90px;
    flex-direction: column;
   
  }
.flows{
  overflow: scroll;
 background-color: rgba(2,2,2,0.1); 
}
.move{
display: flex;}
