  }
@media only screen and (max-width: 1440px) {
  [class*=filtercard]{
    width: 30%;

  } 
  [class*=overall]{
   min-width:60vw;
    max-width:60vw;
    min-height:60vh;
    max-height:60vh
 
  }

}
@media only screen and (max-height:1200px) {
 
  [class*=overall]{
   min-width:58vw;
    max-width:60vw;
    min-height:55vh;
    max-height:56vh
 
  }

} 


@media only screen and (min-width: 1500px) {
  [class*=filtercard]{
    min-width: 14%;
    max-width: 14%;
  }
} 